const burger = document.querySelector(".burger");
if (burger) {
  burger.addEventListener("click", function () {
    burger.classList.toggle("burger-active");
    document.querySelector('.sidebar').classList.toggle("_active");
    document.body.classList.toggle("_lock");
  });
}

let sidebar = document.querySelector('.sidebar')
document.onclick = e => {
  e.preventDefault();
  if (e.target.id !== 'sidebar' && e.target.id !== 'burger') {
    burger.classList.remove('burger-active');
    sidebar.classList.remove('_active');
  }
}

window.addEventListener("scroll", checkedScroll);
window.addEventListener("DOMContentLoaded", checkedScroll);

function checkedScroll() {
  let header = document.querySelector('.header');
  let scrollPos = window.scrollY;

  if (scrollPos > 71) {
    header.classList.add("header__static");
    header.classList.remove("header--main");
  } else {
    header.classList.remove("header__static");
    header.classList.add("header--main");
  }
}
